import React, { useEffect, useState} from 'react';
import { bindActionCreators } from 'redux';
import {connect} from 'react-redux';

import styles from '../css/addCompany.module.css';

import { addCompany, updateCompany, toEditAction,  } from '../../../redux/actions/companiesActions'

const AddCompany = ({addCompany, toEditAction, updateCompany, toEdit, newCompany }) => {

    useEffect(() => {
        toEditAction({
            _id: newCompany._id,
            companyName: newCompany.companyName,
            email: newCompany.email,
            fiscalAddress: newCompany.fiscalAddress,
            cuit: newCompany.cuit,
        });
    }, [newCompany]);

    const onChange = (e) => toEditAction({...toEdit, [e.target.name]: e.target.value });
   
    const onSubmit = (e) => {
        e.preventDefault();
        if(!toEdit._id){
            addCompany({
                companyName: toEdit.companyName, 
                cuit: toEdit.cuit, 
                email: toEdit.email, 
                fiscalAddress: toEdit.fiscalAddress
            });
        }
        else {
            updateCompany(
                toEdit
            )
        };
    };

    return (
        <form onSubmit={onSubmit} className={styles.form}>
            <input 
                type="text" 
                name="companyName" 
                className={styles.input}
                placeholder="Company Name"
                value={toEdit.companyName}
                onChange={onChange}
            />
            <input 
                type="text" 
                name="email" 
                className={styles.input}
                placeholder="Contact Email"  
                value={toEdit.email} 
                onChange={onChange} 
            />
            <input 
                type="text" 
                name="fiscalAddress" 
                className={styles.input}
                placeholder="Fiscal Address"  
                value={toEdit.fiscalAddress} 
                onChange={onChange} 
            />
            <input 
                type="number" 
                name="cuit" 
                className={styles.input}
                placeholder="Cuit"  
                value={toEdit.cuit} 
                onChange={onChange} 
            />
            <input 
                type="submit" 
                value="Submit" 
                className={styles.submitBtn}
            /> 
        </form>
    )
};


const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
        addCompany: addCompany,
        updateCompany: updateCompany,
        toEditAction: toEditAction,
    }, dispatch)
}

const mapStateToProps = state =>{
    return {
        companies: state.companies.list,
        newCompany: state.companies.newCompany,
        isLoading: state.companies.isLoading,
        toEdit: state.companies.toEdit
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(AddCompany);