import React, { Component } from 'react'
import styles from './css/addEditForm.module.css';
import { 
    addBoilers as addBoilersAction,
    updateBoilers as updateBoilersAction,
} from '../../redux/actions/boilersActions';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

export class EditBoiler extends Component {
    state = {
        _id:'',
        description: '',
        boilerType: '',
        building: '',
        maintenancePeriod: '',
        hourMaintenanceCost: '',
        hourEventualCost: '',
        boiler: {},
    }

    componentDidUpdate (prevProps) {
        if (this.props.boiler !== prevProps.boiler) {
            console.log("BOILER",this.props.boiler)
            this.setState({
                _id:this.props.boiler._id,
                description:this.props.boiler.description,
                boilerType:this.props.boiler.boilerType,
                building:this.props.boiler.building,
                maintenancePeriod:this.props.boiler.maintenancePeriod,
                hourMaintenanceCost:this.props.boiler.hourMaintenanceCost,
                hourEventualCost:this.props.boiler.hourEventualCost
            })
        }
    }

    onSubmit = (e) => {
        if (!this.state._id){
            e.preventDefault();
            this.props.addBoiler(
                {
                id:this.state._id,
                description:this.state.description, 
                boilerType:this.state.boilerType, 
                building:this.state.building, 
                maintenancePeriod:this.state.maintenancePeriod, 
                hourMaintenanceCost:this.state.hourMaintenanceCost, 
                hourEventualCost:this.state.hourEventualCost
                }
            );
        } else {
            e.preventDefault();
            this.props.updateBoiler({
                _id:this.state._id,
                description:this.state.description, 
                boilerType:this.state.boilerType, 
                building:this.state.building, 
                maintenancePeriod:this.state.maintenancePeriod, 
                hourMaintenanceCost:this.state.hourMaintenanceCost, 
                hourEventualCost:this.state.hourEventualCost
            }
            );
        }
        this.setState({
            _id: '',
            description: '',
            boilerType: '',
            building: '',
            maintenancePeriod: '',
            hourMaintenanceCost: '',
            hourEventualCost: ''
        });
    }

    onChange = (e) => this.setState({ 
        [e.target.name]: e.target.value
    });
    
    render() {
        return (
            <div className='container'>
                <div className={styles.addBox}>
                    <h2>
                        Add new Boiler
                    </h2>
                </div>
                <form onSubmit={this.onSubmit}>
                    <div className={styles.formBox1}>
                        <label>Description</label>
                        <input 
                            type='text' 
                            name='description' 
                            value = {this.state.description}
                            onChange = {this.onChange}
                        /> 
                        <label>Boiler Type</label>
                        <select onChange={this.onChange} name='boilerType'>
                            <option value='' disabled='disabled' selected={!this.state.boilerType}>
                                Please, select a Boiler Type
                            </option>
                            {
                                this.props.boilersType.map((boilerType) =>
                                    (
                                        <option value={boilerType._id} selected={this.state.boilerType && this.state.boilerType._id && this.state.boilerType._id === boilerType._id}>
                                            {boilerType.description}
                                        </option>
                                    )
                                )
                            }
                        </select>
                        <label>Building</label>
                        <select onChange={this.onChange} name='building'>
                            <option value='' disabled='disabled' selected={!this.state.building}>
                                Please, select a building
                            </option>
                            {
                                this.props.buildings.map((building) =>
                                    (
                                        <option value={building._id} selected={this.state.building && this.state.building._id && this.state.building._id === building._id}>
                                            {building.address}
                                        </option>
                                    )
                                )
                            }
                        </select>
                        <label>Maintenance Period</label>
                        <input 
                            type='text' 
                            name='maintenancePeriod' 
                            value = {this.state.maintenancePeriod}
                            onChange = {this.onChange}
                        />
                        <label>Hour Maintenance Cost</label>
                        <input 
                            type='text' 
                            name='hourMaintenanceCost'
                            value = {this.state.hourMaintenanceCost} 
                            onChange = {this.onChange}
                        />
                        <label>Hour Eventual Cost </label>
                        <input 
                            type='text' 
                            name='hourEventualCost'
                            value = {this.state.hourEventualCost} 
                            onChange = {this.onChange}
                        />
                    </div>
                    <div className={styles.btn}>
                        <input 
                            type='submit' 
                            value='Submit'
                            className='btn'
                        />
                    </div>
                </form>
            </div>
        )
    }
}

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
        addBoiler: addBoilersAction,
        updateBoiler: updateBoilersAction,
    }, dispatch);
};

const mapStateToProps = () => {};

export default connect(mapStateToProps, mapDispatchToProps)(EditBoiler);

