import {
    GET_BOILERTYPES_FETCHING,
    GET_BOILERTYPES_FULFILLED,
    GET_BOILERTYPES_REJECTED,
    ADD_BOILERTYPES_FETCHING,
    ADD_BOILERTYPES_FULFILLED,
    ADD_BOILERTYPES_REJECTED,
    DEL_BOILERTYPES_FETCHING,
    DEL_BOILERTYPES_FULFILLED,
    DEL_BOILERTYPES_REJECTED,
    EDIT_BOILERTYPES_FETCHING,
    EDIT_BOILERTYPES_FULFILLED,
    EDIT_BOILERTYPES_REJECTED,
    SELECT_BOILERTYPES_UPDATE,
} from '../types/boilerTypes_types';

const URL = 'http://localhost:4000/api/v1/types';

// GET BOILER TYPES ACTION
const getBoilerTypesFetching = () => ({
    type: GET_BOILERTYPES_FETCHING,
});

const getBoilerTypesFulfilled = (payload) => ({
    type: GET_BOILERTYPES_FULFILLED,
    payload,
});

const getBoilerTypesRejected = () => ({
    type: GET_BOILERTYPES_REJECTED,
});

export const getBoilerTypes = () => dispatch => {
    dispatch(getBoilerTypesFetching());
    return fetch(URL)
        .then(data => data.json())
        .then(response => {
            dispatch(getBoilerTypesFulfilled(response));
        })
        .catch(() =>{
            dispatch(getBoilerTypesRejected());
        })
}

// DELETE BOILER TYPES ACTION
const delBoilerTypesFetching = () => ({
    type: DEL_BOILERTYPES_FETCHING,
});

const delBoilerTypesFulfilled = (payload) => ({
    type: DEL_BOILERTYPES_FULFILLED,
    payload,
});

const delBoilerTypesRejected = () => ({
    type: DEL_BOILERTYPES_REJECTED,
});

export const delBoilerTypes = id => dispatch => {
    dispatch(delBoilerTypesFetching());
    return fetch(`${URL}/${id}`, {
        method: 'DELETE'
    })
        .then(data => data.json())
        .then(() => {
            dispatch(delBoilerTypesFulfilled(id));
        })
        .catch(() =>{
            dispatch(delBoilerTypesRejected());
        })
}

// ADD BOILER TYPES ACTION
const addBoilerTypesFetching = () => ({
    type: ADD_BOILERTYPES_FETCHING,
});

const addBoilerTypesFulfilled = (payload) => ({
    type: ADD_BOILERTYPES_FULFILLED,
    payload,
});

const addBoilerTypesRejected = () => ({
    type: ADD_BOILERTYPES_REJECTED,
});

export const addBoilerTypes = boilerType => dispatch => {
    dispatch(addBoilerTypesFetching());
    return fetch(URL, {
        method: 'POST',
        body: JSON.stringify(boilerType),
        headers: {
            "Content-type":"application/json"
    }})
        .then(data => data.json())
        .then(() => {
            dispatch(addBoilerTypesFulfilled());
            dispatch(getBoilerTypes())
        })
        .catch(() =>{
            dispatch(addBoilerTypesRejected());
        })
}

// UPDATE BOILER TYPES ACTION
const updateBoilerTypesFetching = () => ({
    type: EDIT_BOILERTYPES_FETCHING,
});

const updateBoilerTypesFulfilled = (payload) => ({
    type: EDIT_BOILERTYPES_FULFILLED,
    payload,
});

const updateBoilerTypesRejected = () => ({
    type: EDIT_BOILERTYPES_REJECTED,
});

export const updateBoilerTypes = typeUpdated => dispatch => {
    dispatch(updateBoilerTypesFetching());
    return fetch(`${URL}/${typeUpdated._id}`, {
        method: 'PUT',
        body: JSON.stringify(typeUpdated),
        headers: {
            "Content-type":"application/json"
    }})
        .then(data => data.json())
        .then(() => {
            dispatch(updateBoilerTypesFulfilled());
            dispatch(getBoilerTypes())
            dispatch(selectBoilerTypes({}))
        })
        .catch(() =>{
            dispatch(updateBoilerTypesRejected());
        })
}

export const selectBoilerTypes = (payload) => ({
    type: SELECT_BOILERTYPES_UPDATE,
    payload,
});
