import {
    GET_APPOINTMENTS_FETCHING,
    GET_APPOINTMENTS_FULFILLED,
    GET_APPOINTMENTS_REJECTED,
    DELETE_APPOINTMENT_FETCHING,
    DELETE_APPOINTMENT_FULFILLED,
    DELETE_APPOINTMENT_REJECTED,
    ADD_APPOINTMENT_FULFILLED,
    ADD_APPOINTMENT_FETCHING,
    ADD_APPOINTMENT_REJECTED,
    UPDATE_ITEM_FETCHING,
    UPDATE_ITEM_FULFILLED,
    UPDATE_ITEM_REJECTED,
    SELECT_ITEM_UPDATE
} from '../types/appointmentsTypes';

const initialState = {
    isLoading: false,
    list: [],
    error: false
};

const appoinmentsReducer = (state = initialState, action) => {
    switch(action.type) {
        case GET_APPOINTMENTS_FETCHING:
            return {
                ...state,
                isLoading: true
            };
        case GET_APPOINTMENTS_FULFILLED:
            return {
                ...state,
                isLoading: false,
                list: action.payload
            };
        case GET_APPOINTMENTS_REJECTED:
            return {
                ...state,
                isLoading: false,
                error: true
            };
        case DELETE_APPOINTMENT_FETCHING:
            return {
                ...state,
                isLoading: true
            };
        case DELETE_APPOINTMENT_FULFILLED:
            return {
                ...state,
                isLoading: false,
                list: state.list.filter(appointments => appointments._id !== action.payload)
            };
        case DELETE_APPOINTMENT_REJECTED:
            return {
                ...state,
                isLoading: false,
                error: true
            };
        case ADD_APPOINTMENT_FETCHING:
            return {
                ...state,
                isLoading: true,
                error: false
            };
        case ADD_APPOINTMENT_FULFILLED:
            return {
                ...state,
                isLoading: false,
                list: [...state.list, action.payload]
            };
        case ADD_APPOINTMENT_REJECTED:
            return {
                ...state,
                isLoading: false,
                error: true
            }
        case UPDATE_ITEM_FETCHING:
            return {
                ...state,
                isLoading: true,
                error: false
            };
        case UPDATE_ITEM_FULFILLED:
            return {
                ...state,
                isLoading: false,
            };
        case UPDATE_ITEM_REJECTED:
            return {
                ...state,
                isLoading: false,
                error: true
            };
        case SELECT_ITEM_UPDATE:
            return {
                ...state,
                itemSelect: action.payload
            };
        default:
            return state;

    };
};

export default appoinmentsReducer;