import React, { useState, useEffect} from 'react'
import TypesTable from '../BoilerTypes/BoilerTypesTable'
import EditType from './AddEditTypes';
import { 
    getBoilerTypes as getBoilerTypesAction,
} from '../../redux/actions/boilerTypesActions';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

const BoilerTypes = ({getBoilerTypes, boilerTypes, typeSelect, error, isLoading}) => {

    useEffect(() =>{
        getBoilerTypes();
    }, []);

    if (isLoading) {
        return <div>Loading...</div>
    }

    if (error) {
        return <div>ERROR!</div>
    }

    return (
        <div>
            <TypesTable boilerTypes={boilerTypes} />
            <EditType boilerType={typeSelect} boilerTypes={boilerTypes} />
        </div>
    );
}

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
        getBoilerTypes: getBoilerTypesAction,
    }, dispatch);
};

const mapStateToProps = state => {
    return {
        isLoading: state.boilerTypes.isLoading,
        boilerTypes: state.boilerTypes.list,
        typeSelect: state.boilerTypes.boilerTypeSelect,
        error: state.boilerTypes.error
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(BoilerTypes);
