import {
    FETCH_COMPANIES_REQ,
    FETCH_COMPANIES_FAIL, 
    FETCH_COMPANIES_SUCCESS,

    DEL_COMPANIES_REQ,
    DEL_COMPANIES_SUCCESS,
    DEL_COMPANIES_FAIL,

    ADD_COMPANIES_REQ,
    ADD_COMPANIES_SUCCESS,
    ADD_COMPANIES_FAIL,

    SELECT_COMPANY,
    TO_EDIT,
    CLEAN_EDIT,

    UPDATE_COMPANY_REQ,
    UPDATE_COMPANY_SUCCESS,
    UPDATE_COMPANY_FAIL,

} from "../types/companiesTypes";

const URL = `http://localhost:4000/api/v1/companies`;


const getCompaniesReq = () => {
    return {
        type: FETCH_COMPANIES_REQ,
    }
}
const getCompaniesSuccess = (payload) => {
    return {
        type: FETCH_COMPANIES_SUCCESS,
        payload
    }
}
const getCompaniesFail = () => {
    return {
        type: FETCH_COMPANIES_FAIL,
        error: 'error fetching the companies'
    }
}


export const getCompanies = () => dispatch =>{
    console.log('GetCompanies Executed');
    dispatch(getCompaniesReq());
    return fetch(URL)
        .then (data => data.json())
        .then(response => {
            dispatch(getCompaniesSuccess(response))
        })
        .catch(error => {
            dispatch(getCompaniesFail(error))
        });
}

const delCompaniesReq = () => {
    return {
        type: DEL_COMPANIES_REQ,
    }
}
const delCompaniesSuccess = (payload) => {
    return {
        type: DEL_COMPANIES_SUCCESS,
        payload
    }
}
const delCompaniesFail = () => {
    return {
        type: DEL_COMPANIES_FAIL,
        error: 'error deleting the companies'
    }
}


export const delCompany = (_id) => dispatch => {
    console.log('DelCompany Executed in Company: ', _id);
    dispatch(delCompaniesReq());
    return fetch(`${URL}/${_id}`, {
        method: 'DELETE'
        })
        .then (data => data.json())
        .then(response => {
            console.log(response);
            dispatch(delCompaniesSuccess(response))
        })
        .then (() => {
            dispatch(getCompanies())
        })
        .catch (error =>{
            dispatch(delCompaniesFail(error))
        })
}

const addCompanyReq = () => {
    return {
        type: ADD_COMPANIES_REQ,
    }
}
const addCompanySuccess = (payload) => {
    return {
        type: ADD_COMPANIES_SUCCESS,
        payload
    }
}
const addCompanyFail = () => {
    return {
        type: ADD_COMPANIES_FAIL,
        error: 'error fetching the companies'
    }
}

export const addCompany = company => dispatch =>{
    console.log('AddCompany Executed');
    dispatch(addCompanyReq());
    return fetch(URL, {
        method: 'POST',
        body: JSON.stringify(company),
        headers: {
            "Content-type":"application/json"
        }
    })
        .then (data => data.json())
        .then(response => {
            dispatch(addCompanySuccess(response))
            dispatch(getCompanies())
            dispatch(cleanToEdit())
        })
        .catch(error => {
            dispatch(addCompanyFail(error))
        });
}


export const companySelected = (payload) => {
    return {
        type: SELECT_COMPANY,
        payload
    }
}

export const toEditAction = (payload) => {
    return {
        type: TO_EDIT,
        payload
    }
}
export const cleanToEdit = () => {
    return {
        type: CLEAN_EDIT
    }
}

const updateCompanyReq = () => {
    return {
        type: UPDATE_COMPANY_REQ
    }
}

const updateCompanySuccess = (payload) => {
    return  {
        type: UPDATE_COMPANY_SUCCESS,
        payload
    }
}

const updateCompanyFail = (error) => {
    return {
        type: UPDATE_COMPANY_FAIL,
        error
    }
}

export const updateCompany = (company) => dispatch => {
    console.log('updateCompany Executed');
    dispatch(updateCompanyReq());
    return fetch(`${URL}/${company._id}`, {
        method: 'PUT',
        body: JSON.stringify(company),
        headers: {
            "Content-type":"application/json"
        }
    })
        .then (data => data.json())
        .then(response => {
            console.log(response)
            dispatch(updateCompanySuccess(response))
            dispatch(getCompanies())
            dispatch(cleanToEdit())
        })
        .catch(error => {
            dispatch(updateCompanyFail(error))
        });
}