import React from 'react';
import propTypes from 'prop-types';
import BuildingItem from './BuildingItem';
import styles from './BuildingsTable.module.css';

const BuildingsTable = (props) => (
    <table className={styles.BuildingsTable}>
        <thead>
            <tr>
                <th>Building Name</th>
                <th>Company Name</th>
                <th>Address</th>
                <th>Phone</th>
                <th>EDIT</th>
                <th>DELETE</th>
            </tr>
        </thead>
        <tbody>
            {props.buildings.map(building => (
                <BuildingItem
                    key={building._id}
                    building={building}
                />
            ))}
        </tbody>
    </table>
);

BuildingsTable.propTypes = {
    buildings: propTypes.array.isRequired
}

export default BuildingsTable;
