import React from 'react';
import CompanyData from '../CompanyItem/CompanyData';
import styles from '../css/companyTable.module.css';
import {connect} from 'react-redux';

const CompanyTable = ({companies}) => {
    return(
    <div className={styles.table}>
        <table>
            <thead>
                <tr>
                    <th>Company Name</th>
                    <th>Email</th>
                    <th>Fiscal Address</th>
                    <th>Cuit</th>
                    <th>Company ID</th>
                    <th>UPDATE</th>
                    <th>DELETE</th>
                </tr>
            </thead>

            <tbody>
                {console.log(companies)}
                {companies.map((company) => 
                    <CompanyData 
                        key={company._id} 
                        company={company} 
                    />
                )}
            </tbody>
        </table>
    </div>
    )
};


const mapStateToProps = state =>{
    return {
        companies: state.companies.list,
    }

}

export default connect(mapStateToProps, null)(CompanyTable);