import {
    GET_BOILERTYPES_FETCHING,
    GET_BOILERTYPES_FULFILLED,
    GET_BOILERTYPES_REJECTED,
    ADD_BOILERTYPES_FETCHING,
    ADD_BOILERTYPES_FULFILLED,
    ADD_BOILERTYPES_REJECTED,
    DEL_BOILERTYPES_FETCHING,
    DEL_BOILERTYPES_FULFILLED,
    DEL_BOILERTYPES_REJECTED,
    EDIT_BOILERTYPES_FETCHING,
    EDIT_BOILERTYPES_FULFILLED,
    EDIT_BOILERTYPES_REJECTED,
    SELECT_BOILERTYPES_UPDATE,
} from '../types/boilerTypes_types';

const initialState = {
    isLoading: false,
    list: [],
    error: false
};

const typesReducer = (state = initialState, action) => {
    switch (action.type) {
        case GET_BOILERTYPES_FETCHING:
            return {
                ...state,
                isLoading: true,
            };
        case GET_BOILERTYPES_FULFILLED:
            return {
                ...state,
                isLoading: false,
                list: action.payload,
            };
        case GET_BOILERTYPES_REJECTED:
            return {
                ...state,
                isLoading: false,
                error: true,
            };
        case DEL_BOILERTYPES_FETCHING:
            return {
                ...state,
                isLoading: true,
            };
        case DEL_BOILERTYPES_FULFILLED:
            return {
                ...state,
                isLoading: false,
                list: state.list.filter((boilerTypes) => {
                    return boilerTypes._id !== action.payload;
                }),
            };
        case DEL_BOILERTYPES_REJECTED:
            return {
                ...state,
                isLoading: false,
                error: true,
            }
        case ADD_BOILERTYPES_FETCHING:
            return {
                ...state,
                isLoading: true,
            };
        case ADD_BOILERTYPES_FULFILLED:
            return {
                ...state,
                isLoading: false,
            };
        case ADD_BOILERTYPES_REJECTED:
            return {
                ...state,
                isLoading: false,
                error: true,
            }
        case EDIT_BOILERTYPES_FETCHING:
            return {
                ...state,
                isLoading: true,
            };
        case EDIT_BOILERTYPES_FULFILLED:
            return {
                ...state,
                isLoading: false,
            };
        case EDIT_BOILERTYPES_REJECTED:
            return {
                ...state,
                isLoading: false,
                error: true,
            };
        case SELECT_BOILERTYPES_UPDATE:
            return {
                ...state,
                boilerTypeSelect: action.payload
            };
        default:
            return state;
    }
}

export default typesReducer;