import {
    GET_BUILDINGS_FETCHING,
    GET_BUILDINGS_FULFILLED,
    GET_BUILDINGS_REJECTED,
    ADD_BUILDING_FETCHING,
    ADD_BUILDING_FULFILLED,
    ADD_BUILDING_REJECTED,
    DELETE_BUILDING_FETCHING,
    DELETE_BUILDING_FULFILLED,
    DELETE_BUILDING_REJECTED,
    EDIT_BUILDING_FETCHING,
    EDIT_BUILDING_FULFILLED,
    EDIT_BUILDING_REJECTED,
    SELECT_BUILDING,
} from '../types/buildings';

import { responseMiddleware } from '../../lib/middlewares';

const URL = 'http://localhost:4000/api/v1/buildings';

// GET BOILERS ACTION
const getBuildingsFetching = () => ({
    type: GET_BUILDINGS_FETCHING,
});

const getBuildingsFulfilled = (payload) => ({
    type: GET_BUILDINGS_FULFILLED,
    payload,
});

const getBuildingsRejected = payload => ({
    type: GET_BUILDINGS_REJECTED,
    payload
});

export const getBuildings = () => dispatch => {
    dispatch(getBuildingsFetching());
    return fetch(URL)
    .then(response => responseMiddleware(response))
    .then(response => {
        dispatch(getBuildingsFulfilled(response));
    })
    .catch(err => {
        dispatch(getBuildingsRejected(err));
    })
}

// ADD
const addBuildingFetching = () => ({
    type: ADD_BUILDING_FETCHING,
});

const addBuildingFulfilled = () => ({
    type: ADD_BUILDING_FULFILLED,
});

const addBuildingRejected = payload => ({
    type: ADD_BUILDING_REJECTED,
    payload,
});

export const addBuilding = boiler => dispatch => {
    dispatch(addBuildingFetching());
    return fetch(URL, {
        method: 'POST',
        body: JSON.stringify(boiler),
        headers: {
            "Content-type": "application/json"
    }})
    .then(response => responseMiddleware(response))
    .then(() => {
        dispatch(addBuildingFulfilled());
        dispatch(selectBuilding({}));
        dispatch(getBuildings());
    })
    .catch(err => {
        dispatch(addBuildingRejected(err.message));
    });
}

// EDIT
const editBuildingFetching = () => ({
    type: EDIT_BUILDING_FETCHING,
});

const editBuildingFulfilled = () => ({
    type: EDIT_BUILDING_FULFILLED,
});

const editBuildingRejected = payload => ({
    type: EDIT_BUILDING_REJECTED,
    payload,
});

export const editBuilding = boiler => dispatch => {
    dispatch(editBuildingFetching());
    return fetch(`${URL}/${boiler._id}`, {
        method: 'PUT',
        body: JSON.stringify(boiler),
        headers: {
            "Content-type": "application/json"
    }})
    .then(response => responseMiddleware(response))
    .then(() => {
        dispatch(editBuildingFulfilled());
        dispatch(selectBuilding({}));
        dispatch(getBuildings());
    })
    .catch(err => {
        dispatch(editBuildingRejected(err.message));
        dispatch(selectBuilding({}));
    });
}

export const selectBuilding = payload => ({
    type: SELECT_BUILDING,
    payload,
});

// DELETE
const delBuildingFetching = () => ({
    type: DELETE_BUILDING_FETCHING,
});

const delBuildingFulfilled = ()=> ({
    type: DELETE_BUILDING_FULFILLED,
});

const delBuildingRejected = payload => ({
    type: DELETE_BUILDING_REJECTED,
    payload
});

export const deleteBuilding = id => dispatch => {
    dispatch(delBuildingFetching());
    return fetch(`${URL}/${id}`, {
        method: 'DELETE'
    })
    .then(response => responseMiddleware(response))
    .then(() => {
        dispatch(delBuildingFulfilled());
        dispatch(getBuildings());
    })
    .catch(err => {
        dispatch(delBuildingRejected(err.message));
    });
};
