import React, { useState, useEffect } from 'react';
import styles from './techniciansList.module.css';
import propTypes from 'prop-types';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { selectItemUpdate as selectItemUpdateAction,
         deleteTechnician as deleteTechnicianAction, 
} from '../../../redux/actions/techniciansActions.js';

const TechniciansList = (props) => {

        const {
            _id,
            firstName,
            lastName,
            phone,
            birthdate,
            capabilities,
            hour_rate,
            monthly_capacity
        } = props.technician;

        return  (
            <tr className={styles.tbody} style={{height: '40px', textAlign: 'center'}}>
                <td>{_id}</td>
                <td>{firstName}</td>
                <td>{lastName}</td>
                <td>{phone}</td>
                <td>{birthdate}</td>
                <td>{capabilities && capabilities.map(capability => {
                    return (`${capability.description} `)
                })}</td>
                <td>{hour_rate}</td>
                <td>{monthly_capacity}</td>
                <td>
                    <button className={styles.edit} onClick={() => props.selectItemUpdate(props.technician)}>Edit</button>
                </td>
                <td>
                    <button className={styles.delete} onClick={() => props.deleteTechnician(_id)}>Delete</button>
                </td>
            </tr>
        )
}

TechniciansList.propTypes = {
    technician: propTypes.object.isRequired
}

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
        selectItemUpdate: selectItemUpdateAction,
        deleteTechnician: deleteTechnicianAction
    }, dispatch);
};

const mapStateToProps = () => {};

export default connect(mapStateToProps, mapDispatchToProps)(TechniciansList);
