import {
    GET_APPOINTMENTS_FETCHING,
    GET_APPOINTMENTS_FULFILLED,
    GET_APPOINTMENTS_REJECTED,
    DELETE_APPOINTMENT_FETCHING,
    DELETE_APPOINTMENT_FULFILLED,
    DELETE_APPOINTMENT_REJECTED,
    ADD_APPOINTMENT_FETCHING,
    ADD_APPOINTMENT_FULFILLED,
    ADD_APPOINTMENT_REJECTED,
    UPDATE_ITEM_FETCHING,
    UPDATE_ITEM_FULFILLED,
    UPDATE_ITEM_REJECTED,
    SELECT_ITEM_UPDATE
} from '../types/appointmentsTypes';

//---- GET APPOINTMENTS ACTIONS ----//

const getAppointmentsFetching = () => ({
    type: GET_APPOINTMENTS_FETCHING
});

const getAppointmentsFulfilled = (payload) => ({
    type: GET_APPOINTMENTS_FULFILLED,
    payload
});

const getAppointmentsRejected = () => ({
    type: GET_APPOINTMENTS_REJECTED
});

export const getAppointments = () => dispatch => {
    dispatch(getAppointmentsFetching());
    return fetch ('http://localhost:4000/api/v1/appointments')
        .then(data => data.json())
        .then(response => {
            dispatch(getAppointmentsFulfilled(response));
        })
        .catch(() =>{
            dispatch(getAppointmentsRejected());
        });
};

//---- DELETE APPOINTMENT ACTIONS ----//

const deleteAppointmentFetching = () => ({
    type: DELETE_APPOINTMENT_FETCHING
});

const deleteAppointmentFulfilled = () => ({
    type: DELETE_APPOINTMENT_FULFILLED
});

const deleteAppointmentRejected = () => ({
    type: DELETE_APPOINTMENT_REJECTED
});

export const deleteAppointment = (id) => dispatch => {
    dispatch(deleteAppointmentFetching());
    return fetch ('http://localhost:4000/api/v1/appointments/' + id, {
        method: 'DELETE'
    })
    .then(response => {
        dispatch(deleteAppointmentFulfilled(response));
        dispatch(getAppointments());
    })
    .catch(() => {
        dispatch(deleteAppointmentRejected())
    });
};

//---- ADD APPOINTMENT ACTIONS ----//

const addAppointmentFetching = () => ({
    type: ADD_APPOINTMENT_FETCHING
});

const addAppointmentFulfilled = (payload) => ({
    type: ADD_APPOINTMENT_FULFILLED,
    payload
});

const addAppointmentRejected = () => ({
    type: ADD_APPOINTMENT_REJECTED
});

export const addNewAppointment = (appointment) => dispatch => {
    console.log("TIPO", appointment)
    dispatch(addAppointmentFetching());
    return fetch ('http://localhost:4000/api/v1/appointments', {
        method: 'POST',
        body: JSON.stringify(appointment),
        headers: {
            "Content-type":"application/json"
        }
    })
    .then(data => data.json())
    .then (response => {
        dispatch(addAppointmentFulfilled(response));
        dispatch(getAppointments());
    })
    .catch(() => {
        dispatch(addAppointmentRejected());
    })
}

//---- UPDATE APPOINTMENT ACTIONS ----//

const updateItemFetching = () => ({
    type: UPDATE_ITEM_FETCHING
});

const updateItemFulfilled = (payload) => ({
    type: UPDATE_ITEM_FULFILLED,
    payload
});

const updateItemRejected = () => ({
    type: UPDATE_ITEM_REJECTED
});

export const selectItemUpdate = (payload) => ({
    type: SELECT_ITEM_UPDATE,
    payload
})

export const updateItem = (appointmentUpdate) => dispatch => {
    console.log("UPDATE FUNCTION", appointmentUpdate)
    dispatch(updateItemFetching());
    return fetch('http://localhost:4000/api/v1/appointments/'+ appointmentUpdate.id, {
        method: 'PUT',
        body: JSON.stringify(appointmentUpdate),
        headers: {
            "Content-type":"application/json"
        }
        })
    .then(data => data.json())
    .then(response => {
        dispatch(updateItemFulfilled(response));
        dispatch(getAppointments());
        dispatch(selectItemUpdate({}));
    })
    .catch(() => {
        dispatch(updateItemRejected());
    });
};