import React, { useEffect, useState } from 'react';
// Components
import BuildingsTable from './BuildingsTable';
import BuildingForm from './BuildingsForm';

// Redux
import { getBuildings as getBuildingsAction } from '../../redux/actions/buildings';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

const Buildings = ({
    isLoading,
    getBuildings,
    buildings,
}) => {
    const [companies, setCompanies] = useState([]);

    useEffect(() =>{
        getBuildings();
        getCompanies();
    }, [] );

    const getCompanies = () => {
        fetch ('http://localhost:4000/company')
            .then(data => data.json())
            .then(response => {
                setCompanies(response)
            })
            .catch(err => {
                console.log(err);
            });
    }

    return (
        <div>
            { isLoading
                ? 'Loading...'
                : (<BuildingsTable buildings={buildings}/>)
            }
            <BuildingForm companies={companies}/>
        </div>
    )
};

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
        getBuildings: getBuildingsAction,
    }, dispatch);
};

const mapStateToProps = state => {
    return {
        isLoading: state.buildings.isLoading,
        buildings: state.buildings.list,
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(Buildings);
