import {
    GET_TECHNICIANS_FETCHING,
    GET_TECHNICIANS_FULFILLED,
    GET_TECHNICIANS_REJECTED,
    DELETE_TECHNICIAN_FETCHING,
    DELETE_TECHNICIAN_FULFILLED,
    DELETE_TECHNICIAN_REJECTED,
    ADD_TECHNICIAN_FULFILLED,
    ADD_TECHNICIAN_FETCHING,
    ADD_TECHNICIAN_REJECTED,
    UPDATE_ITEM_FETCHING,
    UPDATE_ITEM_FULFILLED,
    UPDATE_ITEM_REJECTED,
    SELECT_ITEM_UPDATE
} from '../types/techniciansTypes.js';

const initialState = {
    isLoading: false,
    list: [],
    error: false
};

const techniciansReducer = (state = initialState, action) => {
    switch(action.type) {
        case GET_TECHNICIANS_FETCHING:
            return {
                ...state,
                isLoading: true
            };
        case GET_TECHNICIANS_FULFILLED:
            return {
                ...state,
                isLoading: false,
                list: action.payload
            };
        case GET_TECHNICIANS_REJECTED:
            return {
                ...state,
                isLoading: false,
                error: true
            };
        case DELETE_TECHNICIAN_FETCHING:
            return {
                ...state,
                isLoading: true
            };
        case DELETE_TECHNICIAN_FULFILLED:
            return {
                ...state,
                isLoading: false,
                list: state.list.filter(technicians => technicians._id !== action.payload)
            };
        case DELETE_TECHNICIAN_REJECTED:
            return {
                ...state,
                isLoading: false,
                error: true
            };
        case ADD_TECHNICIAN_FETCHING:
            return {
                ...state,
                isLoading: true,
                error: false
            };
        case ADD_TECHNICIAN_FULFILLED:
            return {
                ...state,
                isLoading: false,
                list: [...state.list, action.payload]
            };
        case ADD_TECHNICIAN_REJECTED:
            return {
                ...state,
                isLoading: false,
                error: true
            }
        case UPDATE_ITEM_FETCHING:
            return {
                ...state,
                isLoading: true,
                error: false
            };
        case UPDATE_ITEM_FULFILLED:
            return {
                ...state,
                isLoading: false,
            };
        case UPDATE_ITEM_REJECTED:
            return {
                ...state,
                isLoading: false,
                error: true
            };
        case SELECT_ITEM_UPDATE:
            return {
                ...state,
                itemSelect: action.payload
            };
        default:
            return state;

    };
};

export default techniciansReducer;