import {
    GET_BOILER_FETCHING,
    GET_BOILER_FULFILLED,
    GET_BOILER_REJECTED,
    ADD_BOILER_FETCHING,
    ADD_BOILER_FULFILLED,
    ADD_BOILER_REJECTED,
    DEL_BOILER_FETCHING,
    DEL_BOILER_FULFILLED,
    DEL_BOILER_REJECTED,
    EDIT_BOILER_FETCHING,
    EDIT_BOILER_FULFILLED,
    EDIT_BOILER_REJECTED,
    SELECT_BOILER_UPDATE,
} from '../types/boilersTypes';

const URL = 'http://localhost:4000/api/v1/boilers';

// GET BOILERS ACTION
const getBoilersFetching = () => ({
    type: GET_BOILER_FETCHING,
});

const getBoilersFulfilled = (payload) => ({
    type: GET_BOILER_FULFILLED,
    payload,
});

const getBoilersRejected = () => ({
    type: GET_BOILER_REJECTED,
});

export const getBoilers = () => dispatch => {
    console.log('GET BOILERS HAS BEEN EXECUTED.');
    dispatch(getBoilersFetching());
    return fetch(URL)
        .then(data => data.json())
        .then(response => {
            dispatch(getBoilersFulfilled(response));
        })
        .catch(() =>{
            dispatch(getBoilersRejected());
        })
}

// DELETE BOILER ACTION
const delBoilersFetching = () => ({
    type: DEL_BOILER_FETCHING,
});

const delBoilersFulfilled = (payload) => ({
    type: DEL_BOILER_FULFILLED,
    payload,
});

const delBoilersRejected = () => ({
    type: DEL_BOILER_REJECTED,
});

export const delBoilers = id => dispatch => {
    console.log('DEL BOILERS HAS BEEN EXECUTED.');
    dispatch(delBoilersFetching());
    return fetch(`${URL}/${id}`, {
        method: 'DELETE'
    })
        .then(data => data.json())
        .then(() => {
            dispatch(delBoilersFulfilled(id));
        })
        .catch(() =>{
            dispatch(delBoilersRejected());
        })
}

// ADD BOILERS ACTION
const addBoilersFetching = () => ({
    type: ADD_BOILER_FETCHING,
});

const addBoilersFulfilled = (payload) => ({
    type: ADD_BOILER_FULFILLED,
    payload,
});

const addBoilersRejected = () => ({
    type: ADD_BOILER_REJECTED,
});

export const addBoilers = boiler => dispatch => {
    console.log('ADD BOILERS HAS BEEN EXECUTED.');
    dispatch(addBoilersFetching());
    return fetch(URL, {
        method: 'POST',
        body: JSON.stringify(boiler),
        headers: {
            "Content-type":"application/json"
    }})
        .then(data => data.json())
        .then((response) => {
            dispatch(addBoilersFulfilled());
            dispatch(getBoilers())
        })
        .catch(() =>{
            dispatch(addBoilersRejected());
        })
}

// UPDATE BOILERS ACTION
const updateBoilersFetching = () => ({
    type: EDIT_BOILER_FETCHING,
});

const updateBoilersFulfilled = (payload) => ({
    type: EDIT_BOILER_FULFILLED,
    payload,
});

const updateBoilersRejected = () => ({
    type: EDIT_BOILER_REJECTED,
});

export const updateBoilers = boilerUpdated => dispatch => {
    console.log('UPDATE BOILERS HAS BEEN EXECUTED.');
    dispatch(updateBoilersFetching());
    return fetch(`${URL}/${boilerUpdated._id}`, {
        method: 'PUT',
        body: JSON.stringify(boilerUpdated),
        headers: {
            "Content-type":"application/json"
    }})
        .then(data => data.json())
        .then((response) => {
            console.log("RESPONSE UPDATE", response)
            dispatch(updateBoilersFulfilled());
            dispatch(getBoilers())
            dispatch(selectBoiler({}))
        })
        .catch(() =>{
            dispatch(updateBoilersRejected());
        })
}

export const selectBoiler = (payload) => ({
    type: SELECT_BOILER_UPDATE,
    payload,
});