import React, { useState, useEffect } from 'react';
import AppointmentsTable from './AppointmentsTable';
import AddEditAppointment from './AddEditAppointment';
import { bindActionCreators } from 'redux';
import {
    getAppointments as getAppointmentsAction,
    addNewAppointment as addAppointmentAction,
} from '../../redux/actions/appointmentsActions';
import { connect } from 'react-redux';

const Appointments = ({getAppointments, isLoading, appointments, error, itemSelect}) => {

    const [buildings, setBuildings] = useState(
        []
    );
    const [boilers, setBoilers] = useState(
        []
    );
    const [technicians, setTechnicians] = useState(
        []
    );

    useEffect(() => {
        getBuildings();
        getAppointments();
        getBoilers();
        getTechnicians();
    },[]);

    if (isLoading) {
        return <div>Loading...</div>
    }
    
    if (error) {
        return <div>Error!</div>
    }

    const getBuildings = () => {
        fetch('http://localhost:4000/api/v1/buildings')
        .then (data => data.json())
        .then(response => {
            console.log("BUILDINGS",response);
            setBuildings(response)
            })
        .catch(err => {
            console.log(err);
        });
    };

    const getBoilers = () => {
        fetch('http://localhost:4000/api/v1/boilers')
        .then (data => data.json())
        .then(response => {
            console.log("BOILERS",response);
            setBoilers(response)
        })
        .catch(err => {
            console.log(err);
        });
    };

    const getTechnicians = () => {
        fetch('http://localhost:4000/technicians')
        .then (data => data.json())
        .then(response => {
            console.log("TECHNICIANS",response);
            setTechnicians(response);
          })    
        .catch (err => {
            console.log(err)
        });
    };

    return (
        <div>
            <AppointmentsTable appointments={appointments}/>
            <AddEditAppointment appointment={itemSelect}
            appointments={appointments}
            buildings={buildings}
            boilers={boilers}
            technicians={technicians}/>
        </div>
    );
};

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
        getAppointments: getAppointmentsAction,
        addNewAppointment: addAppointmentAction,
    }, dispatch);
};

const mapStateToProps = state => {
    return{
        isLoading: state.appointments.isLoading,
        appointments: state.appointments.list,
        itemSelect: state.appointments.itemSelect,
        error: state.appointments.error
    };
  };

export default connect(mapStateToProps, mapDispatchToProps)(Appointments)