import {
    deleteBuilding as deleteBuildingAction,
    selectBuilding as selectBuildingAction,
} from '../../redux/actions/buildings';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import propTypes from 'prop-types';

const BuildingItem = ({
    building,
    deleteBuilding,
    selectBuilding
}) => (
    <tr>
        <td>{building.fullName}</td>
        <td>{building.company && building.company.companyName}</td>
        <td>{building.address}</td>
        <td>{building.phone}</td>
        <td>
            <button onClick={() => deleteBuilding(building._id)}>X</button>
        </td>
        <td>
        <button onClick={() => selectBuilding(building)}>EDIT</button>
        </td>
    </tr>
);

BuildingItem.propTypes = {
    building: propTypes.object.isRequired
}


const mapDispatchToProps = dispatch => {
    return bindActionCreators({
        deleteBuilding: deleteBuildingAction,
        selectBuilding: selectBuildingAction,
    }, dispatch);
};

const mapStateToProps = () => {};

export default connect(mapStateToProps, mapDispatchToProps)(BuildingItem);