import React, { Component } from 'react'
import styles from './css/addEditForm.module.css';
import { 
    addBoilerTypes as addBoilerTypesAction,
    updateBoilerTypes as updateBoilerTypesAction,
} from '../../redux/actions/boilerTypesActions';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

export class EditType extends Component {
    state = {
        _id:'',
        description: '',
        boilerType: {},
    }

    componentDidUpdate (prevProps) {
        if (this.props.boilerType !== prevProps.boilerType) {
            this.setState({
                _id:this.props.boilerType._id,
                description:this.props.boilerType.description,
            })
        }
    }

    onSubmit = (e) => {
        if (!this.state._id){
            e.preventDefault();
            this.props.addType({
                _id:this.state._id,
                description:this.state.description, 
            });
        } else {
            e.preventDefault();
            this.props.updateType({
                _id:this.state._id,
                description:this.state.description, 
            });
        }
        this.setState({
            _id: '',
            description: '',
        });
    }

    onChange = (e) => this.setState({ 
        [e.target.name]: e.target.value
    });
    
    render() {

        return (
            <div className='container'>
                <div className={styles.addBox}>
                    <h2>
                        Add new Boiler Type
                    </h2>
                </div>
                <form onSubmit={this.onSubmit}>
                    <div className={styles.formBox1}>
                        <label>Description</label>
                        <input 
                            type='text' 
                            name='description' 
                            value = {this.state.description}
                            onChange = {this.onChange}
                        /> 
                    </div>
                    <div className={styles.btn}>
                        <input 
                            type='submit' 
                            value='Submit'
                            className='btn'
                        />
                    </div>
                </form>
            </div>
        )
    }
}

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
        addType: addBoilerTypesAction,
        updateType: updateBoilerTypesAction,
    }, dispatch);
};

const mapStateToProps = () => {};

export default connect(mapStateToProps, mapDispatchToProps)(EditType);
