import React, { useState, useEffect } from 'react';
import styles from './techniciansTable.module.css';
import TechniciansList from '../TechniciansList/TechniciansList.js';
import propTypes from 'prop-types';

const TechniciansTable = (props) => {
    return (
        <div>
            <div className={styles.table}>
                <table>
                    <tr>
                        <th>ID</th>
                        <th>First Name</th>
                        <th>Last Name</th>
                        <th>Phone</th>
                        <th>Birthdate</th>
                        <th>Capabilities</th>
                        <th>Hour Rate</th>
                        <th>Monthly Capacity</th>
                    </tr>
                    <tbody>
                        {props.technicians.map( technician => (
                            <TechniciansList
                            key={technician._id}
                            technician={technician} />
                        ))}
                    </tbody>
                </table>
            </div>
      </div>
    )   
}

TechniciansTable.propTypes = {
    technicians: propTypes.array.isRequired
}

export default TechniciansTable;