import React, { useState, useEffect } from 'react'
import TechniciansTable from './TechniciansTable/TechniciansTable';
import AddEditTechnician from './AddEditTechnician/AddEditTechnician.js';
import { bindActionCreators } from 'redux';
import {
    getTechnicians as getTechniciansAction,
    addNewTechnician as addTechnicianAction,
} from '../../redux/actions/techniciansActions';
import { connect } from 'react-redux';

const Technicians = ({getTechnicians, isLoading, technicians, error, itemSelect}) => {

    const [boilerTypes, setBoilerTypes] = useState( [] );

    useEffect(() =>{
        getTechnicians();
        getBoilerTypes();
    }, []);

    if (isLoading) {
        return <div>Loading...</div>
    }
    
    if (error) {
        return <div>ERROR</div>
    }

    const getBoilerTypes = () => {
        fetch ('http://localhost:4000/api/v1/types')
            .then(data => data.json())
            .then(res => {
                setBoilerTypes(res);
            })
            .catch(err => {
                console.log(err);
            });
    }

    return (
        <div>
            <TechniciansTable technicians={technicians} />
            <AddEditTechnician technician={itemSelect}
            technicians={technicians} 
            boilerTypes={boilerTypes} />
        </div>
    )    
}            

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
        getTechnicians: getTechniciansAction,
        addNewTechnician: addTechnicianAction,
    }, dispatch);
};

const mapStateToProps = state => {
    return {
        isLoading: state.technicians.isLoading,
        technicians: state.technicians.list,
        itemSelect: state.technicians.itemSelect,
        error: state.technicians.error
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(Technicians);