import React, { Component } from 'react';
import styles from './addEditTechnician.module.css';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import {
    updateItem as updateItemAction,
    addNewTechnician as addTechnicianAction
} from '../../../redux/actions/techniciansActions.js';

export class AddEditTechnician extends Component {
    state = {
        _id: '',
        firstName: '',
        lastName: '',
        phone: '',
        birthdate: '',
        capabilities: [],
        hour_rate: '',
        monthly_capacity: '',
    };

    componentDidUpdate (prevProps) {   
        if (this.props.technician !== prevProps.technician) {
            this.setState({
                _id: this.props.technician._id,
                firstName: this.props.technician.firstName,
                lastName: this.props.technician.lastName,
                phone: this.props.technician.phone,
                birthdate: this.props.technician.birthdate,
                capabilities: this.props.technician.capabilities || [],
                hour_rate: this.props.technician.hour_rate,
                monthly_capacity: this.props.technician.monthly_capacity
            });
        };
    };

    onSubmit = (e) => {
        if (!this.state._id) {
            e.preventDefault();
            this.props.addNewTechnician({
                id: this.state._id,
                firstName: this.state.firstName,
                lastName: this.state.lastName,
                phone: this.state.phone,
                birthdate: this.state.birthdate,
                capabilities: this.state.capabilities || [],
                hour_rate: this.state.hour_rate,
                monthly_capacity: this.state.monthly_capacity
            });
        } else {
            e.preventDefault();
            this.props.updateItem({
                id: this.state._id,
                firstName: this.state.firstName,
                lastName: this.state.lastName,
                phone: this.state.phone,
                birthdate: this.state.birthdate,
                capabilities: this.state.capabilities || [],
                hour_rate: this.state.hour_rate,
                monthly_capacity: this.state.monthly_capacity
            });
            
        };
        this.setState({
            id: '',
            firstName: '',
            lastName: '',
            phone: '',
            birthdate: '',
            capabilities: [],
            hour_rate: '',
            monthly_capacity: '',
        });
    };

    onChange = (e) => this.setState({ 
        [e.target.name]: e.target.value
    });

    handleCapabilities = (e) => {
        this.setState({
            ...this.state,
            capabilities: [e.target.value]
        });
    }

    render() {
        return (
            <div>
                <div className={styles.addSection}>
                    <h2>Add / Edit Technician</h2>
                    <form onSubmit={this.onSubmit}>
                        <div className={styles.inputSection}>
                            <div className={styles.formCol}>
                                <label>First Name</label>
                                <input 
                                    type = 'text'
                                    name = 'firstName'
                                    placeholder = 'First Name'
                                    value = {this.state.firstName} 
                                    onChange = {this.onChange} 
                                />
                                <label>Last Name</label>
                                <input 
                                    type = 'text'
                                    name = 'lastName'
                                    placeholder = 'Last Name'
                                    value = {this.state.lastName} 
                                    onChange = {this.onChange} 
                                />
                                <label>Phone</label>
                                <input 
                                    type = 'text'
                                    name = 'phone'
                                    placeholder = 'Phone'
                                    value = {this.state.phone} 
                                    onChange = {this.onChange} 
                                />
                                <label>Birthdate</label>
                                <input 
                                    type = 'text'
                                    name = 'birthdate'
                                    placeholder = 'Birthdate'
                                    value = {this.state.birthdate} 
                                    onChange = {this.onChange} 
                                />
                            </div>
                            <div className={styles.formCol}>    
                                <label>Capabilities</label>
                                <select onChange={this.handleCapabilities} name='capabilities'>
                                    <option value='' disabled='disabled' selected={!this.state.capabilities.length > 0}>
                                        Select a capability
                                    </option>
                                    {
                                        this.props.boilerTypes.map((boilerType) =>
                                            (
                                                <option value={boilerType._id} 
                                                selected = {this.state.capabilities && this.state.capabilities[0] && this.state.capabilities[0]._id && this.state.capabilities[0]._id === boilerType._id}>
                                                {boilerType.description}
                                                </option>
                                            )
                                        )
                                    }
                                </select>
                                <label>Hour rate</label>
                                <input 
                                    type = 'text'
                                    name = 'hour_rate'
                                    placeholder = 'Hour Rate'
                                    value = {this.state.hour_rate} 
                                    onChange = {this.onChange} 
                                />
                                <label>Monthly capacity</label>
                                <input 
                                    type = 'text'
                                    name = 'monthly_capacity'
                                    placeholder = 'Monthly capacity'
                                    value = {this.state.monthly_capacity} 
                                    onChange = {this.onChange} 
                                />
                            </div>
                        </div>
                        <div>
                            <input
                                type='submit'
                                value='send'
                                className={styles.submitButton}
                            />
                        </div>
                    </form>
                </div>
            </div>
        );
    };
};

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
        updateItem: updateItemAction,
        addNewTechnician: addTechnicianAction
    }, dispatch);
};

const mapStateToProps = () => {};

export default connect(mapStateToProps, mapDispatchToProps)(AddEditTechnician);
