import React, { useState, useEffect} from 'react'
import propTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { 
    selectBoiler as selectBoilerAction,
    delBoilers as delBoilersAction,
} from '../../redux/actions/boilersActions';

function BoilersList (props) {
    
    return (
        <tr>
            <td>{props.boiler._id}</td>
            <td>{props.boiler.description}</td>
            <td>{props.boiler.boilerType.description}</td>
            <td>{(props.boiler.building && props.boiler.building.address) || ''}</td>
            <td>{props.boiler.maintenancePeriod}</td>
            <td>{props.boiler.hourMaintenanceCost}</td>
            <td>{props.boiler.hourEventualCost}</td>
            <td>
                <button onClick={() =>props.selectItem(props.boiler)}> 
                    Update 
                </button>
                <button onClick={() =>props.deleteBoiler(props.boiler._id)} > 
                    Delete
                </button>
            </td>
        </tr>
    )
}

BoilersList.propTypes = {
    boiler: propTypes.object.isRequired
}

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
        selectItem: selectBoilerAction, 
        deleteBoiler: delBoilersAction
    }, dispatch);
};

const mapStateToProps = () => {};

export default connect(mapStateToProps, mapDispatchToProps)(BoilersList);
