import {
    GET_TECHNICIANS_FETCHING,
    GET_TECHNICIANS_FULFILLED,
    GET_TECHNICIANS_REJECTED,
    DELETE_TECHNICIAN_FETCHING,
    DELETE_TECHNICIAN_FULFILLED,
    DELETE_TECHNICIAN_REJECTED,
    ADD_TECHNICIAN_FETCHING,
    ADD_TECHNICIAN_FULFILLED,
    ADD_TECHNICIAN_REJECTED,
    UPDATE_ITEM_FETCHING,
    UPDATE_ITEM_FULFILLED,
    UPDATE_ITEM_REJECTED,
    SELECT_ITEM_UPDATE
} from '../types/techniciansTypes';

const getTechniciansFetching = () => ({
    type: GET_TECHNICIANS_FETCHING
});

const getTechniciansFulfilled = (payload) => ({
    type: GET_TECHNICIANS_FULFILLED,
    payload
});

const getTechniciansRejected = () => ({
    type: GET_TECHNICIANS_REJECTED
});

export const getTechnicians = () => dispatch => {
    dispatch(getTechniciansFetching());
    return fetch ('http://localhost:4000/api/v1/technicians')
        .then(data => data.json())
        .then(response => {
            dispatch(getTechniciansFulfilled(response));
        })
        .catch(() =>{
            dispatch(getTechniciansRejected());
        });
};

const deleteTechnicianFetching = () => ({
    type: DELETE_TECHNICIAN_FETCHING
});

const deleteTechnicianFulfilled = () => ({
    type: DELETE_TECHNICIAN_FULFILLED
});

const deleteTechnicianRejected = () => ({
    type: DELETE_TECHNICIAN_REJECTED
});

export const deleteTechnician = (id) => dispatch => {
    dispatch(deleteTechnicianFetching());
    return fetch ('http://localhost:4000/api/v1/technicians/' + id, {
        method: 'DELETE'
    })
    .then(response => {
        dispatch(deleteTechnicianFulfilled(response));
        dispatch(getTechnicians());
    })
    .catch(() => {
        dispatch(deleteTechnicianRejected())
    });
};

const addTechnicianFetching = () => ({
    type: ADD_TECHNICIAN_FETCHING
});

const addTechnicianFulfilled = (payload) => ({
    type: ADD_TECHNICIAN_FULFILLED,
    payload
});

const addTechnicianRejected = () => ({
    type: ADD_TECHNICIAN_REJECTED
});

export const addNewTechnician = (technician) => dispatch => {
    console.log("ADD:", technician)
    dispatch(addTechnicianFetching());
    return fetch ('http://localhost:4000/api/v1/technicians', {
        method: 'POST',
        body: JSON.stringify(technician),
        headers: {
            "Content-type":"application/json"
        }
    })
    .then(data => data.json())
    .then (response => {
        dispatch(addTechnicianFulfilled(response));
        dispatch(getTechnicians());
    })
    .catch(() => {
        dispatch(addTechnicianRejected());
    })
}

const updateItemFetching = () => ({
    type: UPDATE_ITEM_FETCHING
});

const updateItemFulfilled = (payload) => ({
    type: UPDATE_ITEM_FULFILLED,
    payload
});

const updateItemRejected = () => ({
    type: UPDATE_ITEM_REJECTED
});

export const selectItemUpdate = (payload) => ({
    type: SELECT_ITEM_UPDATE,
    payload
})

export const updateItem = (technicianUpdate) => dispatch => {
    console.log("UPDATE: ", technicianUpdate)
    dispatch(updateItemFetching());
    return fetch('http://localhost:4000/api/v1/technicians/'+ technicianUpdate.id, {
        method: 'PUT',
        body: JSON.stringify(technicianUpdate),
        headers: {
            "Content-type":"application/json"
        }
        })
    .then(data => data.json())
    .then(response => {
        dispatch(updateItemFulfilled(response));
        dispatch(getTechnicians());
        dispatch(selectItemUpdate({}));
    })
    .catch(() => {
        dispatch(updateItemRejected());
    });
};
