import {
    FETCH_COMPANIES_REQ,
    FETCH_COMPANIES_FAIL, 
    FETCH_COMPANIES_SUCCESS,

    DEL_COMPANIES_REQ,
    DEL_COMPANIES_SUCCESS,
    DEL_COMPANIES_FAIL,

    ADD_COMPANIES_REQ,
    ADD_COMPANIES_SUCCESS,
    ADD_COMPANIES_FAIL,

    UPDATE_COMPANY_REQ,
    UPDATE_COMPANY_SUCCESS,
    UPDATE_COMPANY_FAIL,

    SELECT_COMPANY,
    TO_EDIT,
    CLEAN_EDIT,
} from "../types/companiesTypes";

const initialState = {
    isLoading: false,
    list: [],
    newCompany: {},
    toEdit: {
        _id: '',
        companyName: '',
        cuit: '',
        email: '',
        fiscalAddress: ''
    },
    error: '',
    msg: ''
}

const companiesReducer = (state = initialState, action) => {
    switch(action.type) {
        case FETCH_COMPANIES_REQ:
            return {
                ...state,
                isLoading: true
            }
        case FETCH_COMPANIES_SUCCESS:
            return {
                ...state,
                isLoading: false,
                list: action.payload
            }
        case FETCH_COMPANIES_FAIL:
            return {
                ...state,
                isLoading: false,
                error: action.error
            }

        case DEL_COMPANIES_REQ:
            return {
                ...state,
                isLoading: true
            }
        case DEL_COMPANIES_SUCCESS:
            return {
                ...state,
                isLoading: false,
                msg: action.payload
            }
        case DEL_COMPANIES_FAIL:
            return {
                ...state,
                isLoading: false,
                error: action.error
            }

        case ADD_COMPANIES_REQ:
            return {
                ...state,
                isLoading: true
            }
        case ADD_COMPANIES_SUCCESS:
            return {
                ...state,
                isLoading: false,
                list: [...state.list, action.payload]
            }
        case ADD_COMPANIES_FAIL:
            return {
                ...state,
                isLoading: false,
                error: action.error
            }
        
        case SELECT_COMPANY:
            return {
                ...state,
                newCompany:action.payload
            }
        
        case TO_EDIT:
            return{
                ...state,
                toEdit: action.payload
            }
        
        case CLEAN_EDIT:
            return {
                ...state,
                newCompany:{},
                toEdit: {
                    _id: '',
                    companyName: '',
                    cuit: '',
                    email: '',
                    fiscalAddress: ''
                },
        }

        case UPDATE_COMPANY_REQ:
            return {
                ...state,
                isLoading: true
            }
        case UPDATE_COMPANY_SUCCESS:
            return {
                ...state,
                isLoading: false,
                msg: action.payload
            }
        case UPDATE_COMPANY_FAIL:
            return {
                ...state,
                isLoading: false,
                error: action.error
            }
        default:
            return state
    }

}

export default companiesReducer;