import React from 'react'
import propTypes from 'prop-types';
import styles from '../css/companyItem.module.css';
import { bindActionCreators } from 'redux';
import {connect} from 'react-redux';
import { delCompany, companySelected } from '../../../redux/actions/companiesActions';


const CompanyData = ({delCompany, company, companies, companySelected}) => {
    const {_id, companyName, cuit, email, fiscalAddress} = company;

    const selectCompany = (_id) => {
        const selected = companies.find(company => company._id === _id);
        console.log(selected);
        companySelected(selected)
    };

    return (
        <tr className={styles.tRow}>
            <td className={styles.data}>{companyName}</td>
            <td className={styles.data}>{email}</td>
            <td className={styles.data}>{fiscalAddress}</td>
            <td className={styles.data}>{cuit}</td>
            <td className={styles.data}>{_id}</td>

            <td className={styles.btn}>
                <button className={styles.xBtn} 
                onClick={() => selectCompany(_id)}
                >
                    UPDATE
                </button>
            </td>
            <td className={styles.btn}>
                <button className={styles.xBtn} 
                onClick={() => delCompany(_id)}
                >
                    DELETE
                </button>
            </td>
        </tr>
    )
};

CompanyData.propTypes = {
    company: propTypes.object.isRequired,
};

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
        delCompany: delCompany,
        companySelected: companySelected
    }, dispatch)
}

const mapStateToProps = state =>{
    return {
        companies: state.companies.list
    }

}

export default connect(mapStateToProps, mapDispatchToProps)(CompanyData);