import React, { useState, useEffect} from 'react'
import BoilersTable from '../Boilers/BoilersTable'
import EditBoiler from './AddEditBoilers';
import { 
    getBoilers as getBoilersAction,
} from '../../redux/actions/boilersActions';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

const Boilers = ({ getBoilers, isLoading, boilers, error, boiler }) => {

    const [buildings, setBuildings] = useState (
        []
    );
    const [boilersType, setBoilersType] = useState (
        []
    );

    useEffect(() => {
        getBoilers();
        getBuildings();
        getBoilersType();
    }, []);

    if (isLoading) {
        return <div>Loading...</div>
    }

    if (error) {
        return <div>ERROR!</div>
    }

    const getBuildings = () => {
        fetch ('http://localhost:4000/api/v1/buildings')
            .then(data => data.json())
            .then(response => {
                console.log("RESPONSE", response)
                setBuildings(response)
            })
            .catch(err => {
                console.log(err);
            });
    }

    const getBoilersType = () => {
        fetch ('http://localhost:4000/types')
            .then(data => data.json())
            .then(response => {
                console.log("RESPONSE", response)
                setBoilersType(response)
            })
            .catch(err => {
                console.log(err);
            });
    }

    return (
        <div>
            <BoilersTable boilers={boilers} />
            <EditBoiler boiler={boiler} boilers={boilers} buildings={buildings} boilersType={boilersType} />
        </div>
    );
}

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
        getBoilers: getBoilersAction,
    }, dispatch);
};

const mapStateToProps = state => {
    return {
        isLoading: state.boilers.isLoading,
        boilers: state.boilers.list,
        boiler: state.boilers.boilerSelect,
        error: state.boilers.error
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(Boilers);