import { combineReducers } from 'redux';
//Import the reducers here 
// Resources
import appointmentsReducer from './appointmentsReducer';
import boilersReducer from './boilersReducer';
import typesReducer from './boilerTypesReducers';
import buildingsReducer from './buildings';
import companiesReducer from './companiesReducer';
import techniciansReducer from './techniciansReducer';
// UI
import modalReducer from './modalReducer';

const rootReducer = combineReducers({
    // Code your reducers here( Ex: company: companyReducer)
    appointments: appointmentsReducer,
    boilers: boilersReducer,
    boilerTypes: typesReducer,
    buildings: buildingsReducer,
    companies: companiesReducer,
    modal: modalReducer,
    technicians: techniciansReducer,
});

export default rootReducer;
