import React, { useState, useEffect } from 'react';
import propTypes from 'prop-types';
import { connect} from 'react-redux';
import { bindActionCreators } from 'redux';
import { selectItemUpdate as selectItemUpdateAction,
         deleteAppointment as deleteAppointmentAction, 
} from '../../redux/actions/appointmentsActions';

const AppointmentList = (props) => {
        const {
            _id,
            building,
            boiler,
            technician,
            start_timestamp,
            end_timestamp,
            monthly_hours,
            maintainceType
        } = props.appointment;

        return (  
            <tr style={{height: '40px', textAlign: 'center'}}>
                <td>{_id}</td>
                <td>{building && building.fullName}</td>
                <td>{boiler && boiler.description}</td>
                <td>{technician.firstName} - {technician.lastName}</td>
                <td>{start_timestamp}</td>
                <td>{end_timestamp}</td>
                <td>{monthly_hours}</td>
                <td>{maintainceType}</td>
                <td>
                    <button onClick={() => props.selectItemUpdate(props.appointment)}>Update</button>
                </td>
                <td>
                    <button onClick={() => props.deleteAppointment(_id)}>Delete</button>
                </td>
            </tr>
        );  
};

AppointmentList.propTypes = {
    appointment: propTypes.array.isRequired
};

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
        selectItemUpdate: selectItemUpdateAction,
        deleteAppointment: deleteAppointmentAction
    },dispatch);
};

const mapStateToProps = () => {};

export default connect(mapStateToProps, mapDispatchToProps)(AppointmentList)
