import { useState, useEffect } from 'react';
import {
    addBuilding as addBuildingAction,
    editBuilding as editBuildingAction,
} from '../../redux/actions/buildings';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import styles from './BuildingsForm.module.css';

const BuildingsForm = ({
    building,
    companies = [],
    error,
    errorMessage,
    addBuilding,
    editBuilding,
}) => {
    const [fullName, setFullName] = useState('');
    const [companyId, setCompanyId] = useState('');
    const [phone, setPhone] = useState('');
    const [address, setAddress] = useState('');

    useEffect(() => {
        setFullName(building.fullName);
        building.company && setCompanyId(building.company._id);
        setPhone(building.phone);
        setAddress(building.address);
    }, [ building._id ])

    const handleSubmit = (e) => {
        e.preventDefault();
        const payload = {
            _id: building._id || '',
            fullName,
            phone,
            address,
            company: companyId,
        };
        building._id ? editBuilding(payload) : addBuilding(payload);
        clearForm();
    }

    const clearForm = () => {
        setFullName('');
        setCompanyId('');
        setPhone('');
        setAddress('');
    };

    return (
        <form className={styles.BuildingForm} onSubmit={handleSubmit}>
            { error && (<p className={styles.ErrorMessage}>There was an error! {errorMessage}</p>) }
            <fieldset>
                <label>Building name:</label>
                <input type="text" name="fullName" value={fullName} onChange={(e) => setFullName(e.target.value)} required />
            </fieldset>
            <fieldset>
                <label>Company:</label>
                <select name="company" value={companyId} defaultValue={companyId} onChange={(e) => setCompanyId(e.target.value)} required>
                    <option value="">Please select a company</option>
                    {
                        companies.length > 0
                        && companies.map(company => (
                            <option key={company._id} value={company._id}>{company.companyName ||''}</option>
                        ))
                    }
                </select>
            </fieldset>
            <fieldset>
                <label>Address:</label>
                <input type="text" name="address" value={address} onChange={(e) => setAddress(e.target.value)} required />
            </fieldset>
            <fieldset>
                <label>Telephone:</label>
                <input type="tel" name="phone" value={phone} onChange={(e) => setPhone(e.target.value)} required />
            </fieldset>
            <button type="submit">{ building._id ? 'Edit': 'Add'}</button>
        </form>
    );
}

const mapDispatchToProps = dispatch => {
    return bindActionCreators({
        addBuilding: addBuildingAction,
        editBuilding: editBuildingAction,
    }, dispatch);
};

const mapStateToProps = state => {
    return {
        building: state.buildings.selected,
        error: state.buildings.error,
        errorMessage: state.buildings.errorMessage,
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(BuildingsForm);
