import React, { useState, useEffect} from 'react'
import propTypes from 'prop-types';
import BoilersList from './BoilersList';
import styles from './css/boilersTable.module.css';

function BoilersTable (props) {
   
    return (
        <div>
            <div className={styles.appList}> 
                <table>
                    <thead>
                        <tr>
                            <th>ID</th>
                            <th>Description</th>
                            <th>Boiler Type</th>
                            <th>Building</th>
                            <th>Maintenance Period</th>
                            <th>Hour Maintenance Cost</th>
                            <th>Hour Eventual Cost</th>
                            <th>Actions</th>
                        </tr>
                    </thead>
                    <tbody>
                        {props.boilers.map((boiler) => (
                            <BoilersList key={boiler.id} boiler={boiler} />
                        ))}
                    </tbody>
                </table>
            </div>
        </div>
    );
}

BoilersTable.propTypes = {
    boilers: propTypes.array.isRequired
}

export default BoilersTable;
