import {
    GET_BUILDINGS_FETCHING,
    GET_BUILDINGS_FULFILLED,
    GET_BUILDINGS_REJECTED,
    ADD_BUILDING_FETCHING,
    ADD_BUILDING_FULFILLED,
    ADD_BUILDING_REJECTED,
    DELETE_BUILDING_FETCHING,
    DELETE_BUILDING_FULFILLED,
    DELETE_BUILDING_REJECTED,
    EDIT_BUILDING_FETCHING,
    EDIT_BUILDING_FULFILLED,
    EDIT_BUILDING_REJECTED,
    SELECT_BUILDING,
} from '../types/buildings';

const initialState = {
    isLoading: false,
    error: false,
    list: [],
    selected: {},
};

const buildingsReducer = (state = initialState, action) => {
    switch (action.type) {
        case GET_BUILDINGS_FETCHING:
            return {
                ...state,
                isLoading: true,
            };
        case GET_BUILDINGS_FULFILLED:
            return {
                ...state,
                isLoading: false,
                error: false,
                list: action.payload,
            };
        case GET_BUILDINGS_REJECTED:
            return {
                ...state,
                isLoading: false,
                error: true,
                errorMessage: action.payload
            };
        case DELETE_BUILDING_FETCHING:
            return {
                ...state,
                isLoading: true,
            };
        case DELETE_BUILDING_FULFILLED:
            return {
                ...state,
                error: false,
                errorMessage: '',
                isLoading: false,
            };
        case DELETE_BUILDING_REJECTED:
            return {
                ...state,
                isLoading: false,
                error: true,
                errorMessage: action.payload
            };
        case ADD_BUILDING_FETCHING:
            return {
                ...state,
                isLoading: true,
            };
        case ADD_BUILDING_FULFILLED:
            return {
                ...state,
                error: false,
                errorMessage: '',
                isLoading: false,
            };
        case ADD_BUILDING_REJECTED:
            return {
                ...state,
                isLoading: false,
                error: true,
                errorMessage: action.payload
            };
        case EDIT_BUILDING_FETCHING:
            return {
                ...state,
                isLoading: true,
            };
        case EDIT_BUILDING_FULFILLED:
            return {
                ...state,
                error: false,
                errorMessage: '',
                isLoading: false,
            };
        case EDIT_BUILDING_REJECTED:
            return {
                ...state,
                isLoading: false,
                error: true,
                errorMessage: action.payload
            };
        case SELECT_BUILDING:
            return {
                ...state,
                selected: action.payload,
            };
        default:
            return state;
    }
}

export default buildingsReducer;