import React, { useState, useEffect} from 'react'
import propTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { 
    selectBoilerTypes as selectBoilerTypesAction,
    delBoilerTypes as delBoilerTypesAction,
} from '../../redux/actions/boilerTypesActions';

function TypesList (props) {

    return (
        <tr>
            <td>{props.boilerType._id}</td>
            <td>{props.boilerType.description}</td>
            <td>
                <button onClick={() =>props.selectItem(props.boilerType)}> 
                    Update 
                </button>
                <button onClick={() =>props.deleteType(props.boilerType._id)} > 
                    Delete
                </button>
            </td>
        </tr>
    )
}

TypesList.propTypes = {
    boilerType: propTypes.object.isRequired
}

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
        selectItem: selectBoilerTypesAction, 
        deleteType: delBoilerTypesAction
    }, dispatch);
};

const mapStateToProps = () => {};

export default connect(mapStateToProps, mapDispatchToProps)(TypesList);
