import React, { Component } from 'react';
import styles from './css/add-edit-form.module.css';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import {
    updateItem as updateItemAction,
    addNewAppointment as addAppointmentAction
} from '../../redux/actions/appointmentsActions';

export class EditAppointment extends Component {
    state = {
        _id: '',
        building: '',
        boiler: '',
        technician:'',
        start_timestamp:'',
        end_timestamp:'',
        monthly_hours:'',
        maintainceType:'',
        appointment: {},
    };

    componentDidUpdate (prevProps) {   
        if (this.props.appointment !== prevProps.appointment) {
            this.setState({
                _id: this.props.appointment._id,
                building: this.props.appointment.building._id,
                boiler: this.props.appointment.boiler._id,
                technician: this.props.appointment.technician._id,
                start_timestamp: this.props.appointment.start_timestamp,
                end_timestamp: this.props.appointment.end_timestamp,
                monthly_hours: this.props.appointment.monthly_hours,
                maintainceType: this.props.appointment.maintainceType
            });
        };
    };

    onSubmit = (e) => {
        if (!this.state._id) {
            e.preventDefault();
            this.props.addNewAppointment({
                id: this.state._id,
                building: this.state.building,
                boiler: this.state.boiler,
                technician: this.state.technician,
                start_timestamp: this.state.start_timestamp,
                end_timestamp: this.state.end_timestamp,
                monthly_hours: this.state.monthly_hours,
                maintainceType: this.state.maintainceType
            });
        } else {
            e.preventDefault();
            this.props.updateItem({
                id: this.state._id,
                building: this.state.building,
                boiler: this.state.boiler,
                technician: this.state.technician,
                start_timestamp: this.state.start_timestamp,
                end_timestamp: this.state.end_timestamp,
                monthly_hours: this.state.monthly_hours,
                maintainceType: this.state.maintainceType
            });
            
        };
        this.setState({
            _id: '',
            building: '',
            boiler: '',
            technician:'',
            start_timestamp:'',
            end_timestamp:'',
            monthly_hours:'',
            maintainceType:''
        });
    };

    onChange = (e) => this.setState({ 
        [e.target.name]: e.target.value
    });

    render() {
        return (
            <div>
                <div>
                <div className={styles.addBox}>
                    <h2>
                        Add/Edit appointment
                    </h2>
                    <form onSubmit={this.onSubmit}>
                        <div className={styles.formBox1}>
                            <p>
                                Building
                            </p>
                            <select onChange={this.onChange} name='building'>
                                <option value='' disabled="disabled" selected={!this.state.building}>
                                    Please, select a building
                                </option>
                                {
                                    this.props.buildings.map((building)=>
                                        (
                                            <option value={building._id}
                                                selected={
                                                    this.state.building &&
                                                    this.state.building === building._id}
                                            >
                                                    {building.fullName} - {building.address}
                                            </option>
                                        ) 
                                    )
                                }    
                               
                            </select>
                            <p>
                                Boiler
                            </p>
                            <select onChange={this.onChange} name='boiler'>
                                <option value='' disabled='disabled' selected={!this.state.boiler}>
                                    Please, select a boiler
                                </option>
                                {
                                    this.props.boilers.map((boiler) =>
                                        (
                                            <option value={boiler._id}
                                                selected={
                                                    this.state.boiler &&
                                                    this.state.boiler === boiler._id}
                                            >
                                                    {boiler.description}
                                            </option>
                                        )
                                    )
                                }
                            </select>
                            <p>
                                Technician
                            </p>
                            <select onChange={this.onChange} name='technician'>
                                <option value='' disabled='disabled' selected={!this.state.technician}>
                                    Please, select a technician
                                </option>
                                {
                                        this.props.technicians.map((technician) =>
                                        (
                                            <option value={technician._id}
                                                selected={
                                                    this.state.technician &&
                                                    this.state.technician === technician._id}
                                            >
                                                    {technician.firstName} {technician.lastName}
                                            </option>
                                        )
                                    )
                                }
                            </select>
                        </div>
                        <div className='form-box1'>
                            <p>
                                Start time
                            </p>
                            <input
                                name='start_timestamp'
                                type='text' value={this.state.start_timestamp}
                                onChange={this.onChange}
                            />
                            <p>
                                End time
                            </p>
                            <input
                                name='end_timestamp'
                                type='text' value={this.state.end_timestamp}
                                onChange={this.onChange}
                            />
                            <p>
                                Monthly hours
                            </p>
                            <input
                                name='monthly_hours'
                                type='number' value={this.state.monthly_hours}
                                onChange={this.onChange}
                            />
                            <p>
                                Maintenance type
                            </p>
                            <input
                                name='maintainceType'
                                type='text' value={this.state.maintainceType}
                                onChange={this.onChange}
                            />
                        </div>
                        <div className={styles.btn}>
                        <input type='submit' value='Submit'/>
                    </div>
                    </form>
                </div>
            </div>
            </div>
        );
    };
};

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({
        updateItem: updateItemAction,
        addNewAppointment: addAppointmentAction
    }, dispatch);
};

const mapStateToProps = () => {};

export default connect(mapStateToProps, mapDispatchToProps)(EditAppointment)